@use 'base/mixins' as m;
@use 'base/variables' as v;

.acciones{
    margin-top: 5rem;
    @include m.tablet{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    
    a{
        display:block;
        text-align: center;
        margin-bottom: 2rem;
        color: v.$negro;
        &:last-of-type{
            margin-bottom:0;
        }
    }
}